import {css} from 'lit-element';

export const style = css`button{position:relative;border:none;width:auto;border-radius:var(--border-button-radius);padding:0 3em;margin:0rem;font-family:inherit;font-size:1em;font-weight:var(--button-font-weight);transition:all .4s;height:3.3rem;color:var(--color-white);box-shadow:inset 0px 0px 0px 0px var(--color-primary);background-color:var(--color-primary);cursor:pointer;outline-style:solid;outline-width:1px;outline-color:transparent}buttonbutton::-moz-focus-inner{border:0}button[disabled]{background-color:var(--color-background);cursor:not-allowed}button.primary{color:var(--color-white);box-shadow:inset 0px 0px 0px 0px var(--color-primary);background-color:var(--color-primary)}button.primary:not([disabled]):hover,button.primary:not([disabled]):focus{background-color:var(--color-primary-dark);box-shadow:inset 0px 0px 0px var(--border-width-hover) var(--color-primary)}button.primary:not([disabled]):active{box-shadow:inset 0px 0px 20px 10px var(--color-primary)}button.secondary{color:var(--color-primary);background-color:var(--color-secondary);box-shadow:inset 0px 0px 0px 0px var(--color-secondary-dark)}button.secondary:not([disabled]):hover,button.secondary:not([disabled]):focus{box-shadow:inset 0px 0px 0px var(--border-width-hover) var(--color-secondary-dark)}button.secondary:not([disabled]):active{box-shadow:inset 0px 0px 20px 10px var(--color-secondary-dark)}button.secondary[disabled]{color:var(--color-label)}button.secondary.dark{color:var(--color-white);background-color:var(--color-secondary-darktheme);box-shadow:inset 0px 0px 0px 0px var(--color-secondary-darktheme)}button.secondary.dark:not([disabled]):hover,button.secondary.dark:not([disabled]):focus{box-shadow:inset 0px 0px 0px var(--border-width-hover) var(--color-secondary-darktheme)}button.secondary.dark:not([disabled]):active{box-shadow:inset 0px 0px 20px 10px var(--color-secondary-darktheme)}button.tertiary{padding:0;background:transparent;color:var(--color-primary);border:none;box-shadow:none;border-bottom:1px solid transparent;border-radius:0;height:3.3rem}button.tertiary:not([disabled])::after{content:"";background:var(--color-primary);position:absolute;left:0;height:1px;bottom:0;width:0%;transition:width .3s}button.tertiary:not([disabled]):hover::after,button.tertiary:not([disabled]):focus::after{width:100%}button.tertiary[disabled]{color:var(--color-label)}button.tertiary.dark{color:var(--color-white)}button.tertiary.dark:not([disabled])::after{background:var(--color-white)}button.full-width{width:100%;padding:0}`;