import {css} from 'lit-element';

export const style = css`.jsd-radio-wrapper{margin-bottom:10px}.jsd-radio-wrapper .jsd-radio{display:block;padding:.5rem 1.5rem .5rem 0}.jsd-radio-wrapper .jsd-radio:first-child{padding-top:0}.jsd-radio-wrapper .jsd-radio:last-child{padding-bottom:0}.jsd-radio-wrapper .jsd-radio label{display:inline-block;color:var(--color-label);cursor:pointer;z-index:1}.jsd-radio-wrapper .jsd-radio label .radio{display:inline-block;vertical-align:middle;position:relative;height:calc(1rem + 2px);width:calc(1rem + 2px);margin-right:.25rem;margin-top:-0.1rem;border-radius:50%;border:1px solid var(--color-border);background-color:var(--color-secondary)}.jsd-radio-wrapper .jsd-radio label .radio:after{content:"";position:absolute;height:10px;width:10px;border-radius:50%;top:calc(50% - 5px);left:calc(50% - 5px);box-sizing:border-box;background-color:var(--color-primary);transform:scale(0);transition:transform .2s}.jsd-radio-wrapper .jsd-radio label.disabled{cursor:not-allowed;opacity:.8}.jsd-radio-wrapper .jsd-radio label:not(.disabled):hover{color:var(--color-black)}.jsd-radio-wrapper .jsd-radio label:not(.disabled):hover .radio{background-color:transparent}.jsd-radio-wrapper .jsd-radio .radio-input{position:absolute;margin:0;opacity:0}.jsd-radio-wrapper .jsd-radio .radio-input:active+label:not(.disabled) .radio,.jsd-radio-wrapper .jsd-radio .radio-input:focus+label:not(.disabled) .radio{background-color:transparent;border-color:var(--color-primary)}.jsd-radio-wrapper .jsd-radio .radio-input:checked+label .radio{border-color:var(--color-primary)}.jsd-radio-wrapper .jsd-radio .radio-input:checked+label .radio::after{transform:scale(1)}.jsd-radio-wrapper .jsd-radio .radio-input:checked+label:hover+label:not(.disabled) .radio{background-color:transparent}.jsd-radio-wrapper .jsd-radio .radio-input:checked+label:hover+label:not(.disabled) .radio::after{background-color:var(--color-primary-dark)}.jsd-radio-wrapper.inline .jsd-radio{display:inline-block;padding-top:0}.jsd-radio-wrapper.error .jsd-radio label .radio{border-color:var(--color-error);background-color:var(--color-error-background)}.jsd-radio-wrapper.dark .jsd-radio label{color:var(--color-label-darktheme)}.jsd-radio-wrapper.dark .jsd-radio label .radio{background-color:var(--color-secondary-darktheme);border-color:var(--color-secondary-dark-darktheme)}.jsd-radio-wrapper.dark .jsd-radio label.disabled{color:var(--color-label-darktheme)}.jsd-radio-wrapper.dark .jsd-radio label.disabled .radio{background-color:var(--color-secondary-darktheme);border-color:var(--color-secondary-dark-darktheme)}.jsd-radio-wrapper.dark .jsd-radio:hover label:not(.disabled){color:var(--color-white)}.jsd-radio-wrapper.dark.error .jsd-radio label .radio{border-color:var(--color-error);background-color:var(--color-secondary-darktheme)}.jsd-radio-wrapper.dark.error .jsd-radio:hover label:not(.disabled) .radio{background-color:transparent}.jsd-radio-wrapper.dark .radio-input:checked+label{color:var(--color-white)}`;