import {css} from 'lit-element';

export const style = css`.jsd-select-wrapper{position:relative;min-height:5.1rem;height:auto}.jsd-select-wrapper .empty-label{padding:1px;margin:-1px}.jsd-select-wrapper .select-wrapper{position:absolute;overflow:hidden;box-sizing:border-box;outline:solid 1px transparent}.jsd-select-wrapper .select-wrapper.focus{background-color:var(--color-white)}.jsd-select-wrapper .select-wrapper.focus:not(.error){border:1px solid var(--color-primary)}.jsd-select-wrapper .select-wrapper>.button,.jsd-select-wrapper .select-wrapper .input{position:absolute;width:100%;height:calc(3.3rem - 2px);padding:1rem;background:transparent;border:none;z-index:1;cursor:pointer;text-align:left;font-size:1rem;outline:solid 1px transparent;line-height:calc(1.3rem - 2px);color:var(--color-placeholder);box-sizing:border-box}.jsd-select-wrapper .select-wrapper>.button.disabled,.jsd-select-wrapper .select-wrapper .input.disabled{cursor:not-allowed}.jsd-select-wrapper .select-wrapper>.button::-moz-focus-inner,.jsd-select-wrapper .select-wrapper .input::-moz-focus-inner{border:0}.jsd-select-wrapper .select-wrapper>.button::after,.jsd-select-wrapper .select-wrapper .input::after{content:"";border:solid var(--color-placeholder);border-width:0 1px 1px 0;display:inline-block;padding:3px;position:absolute;top:1.3rem;right:1rem;transform:rotate(45deg);transition:transform .3s}.jsd-select-wrapper .select-wrapper>.input{padding:0}.jsd-select-wrapper .select-wrapper>.input>.display-text{position:absolute;width:100%;height:100%;border:none;background:transparent;padding:1rem;box-sizing:border-box;color:var(--color-black)}.jsd-select-wrapper .select-wrapper>.input>input{position:absolute;width:100%;height:100%;border:none;background:transparent;padding:1rem;box-sizing:border-box;font-size:1rem;outline:none}.jsd-select-wrapper .select-wrapper>.input>input::placeholder{color:transparent}.jsd-select-wrapper .select-wrapper>.input>input:focus,.jsd-select-wrapper .select-wrapper>.input>input.active{background:var(--color-white)}.jsd-select-wrapper .select-wrapper>.input>input:focus::placeholder,.jsd-select-wrapper .select-wrapper>.input>input.active::placeholder{color:var(--color-placeholder)}.jsd-select-wrapper .select-wrapper ul{position:relative;top:0;list-style:none;padding:0;margin:0;margin-top:calc(3.3rem - 2px);outline:none;border:none;width:100%;height:auto;max-height:0;font-size:1rem;color:var(--color-black);transition:all .4s;box-sizing:border-box;overflow:hidden;transition:max-height .3s;overflow-y:auto}.jsd-select-wrapper .select-wrapper ul li{position:relative;height:calc(3.3rem - 2px);line-height:calc(1.3rem - 2px);padding:1rem;padding-left:2.1rem;box-sizing:border-box;cursor:pointer}.jsd-select-wrapper .select-wrapper ul li.current,.jsd-select-wrapper .select-wrapper ul li:hover{background-color:var(--color-secondary)}.jsd-select-wrapper .select-wrapper ul li.selected{background-color:var(--color-primary-fill)}.jsd-select-wrapper .select-wrapper ul li.selected::before{content:"";border:solid var(--color-primary);border-width:0 2px 2px 0;display:inline-block;padding:5px 2px;position:absolute;top:1.1rem;left:1rem;transform:rotate(45deg)}.jsd-select-wrapper .select-wrapper.disabled>.button,.jsd-select-wrapper .select-wrapper.disabled .input{opacity:.8}.jsd-select-wrapper .select-wrapper.expanded{height:auto;background-color:var(--color-white);z-index:10}.jsd-select-wrapper .select-wrapper.expanded:not(.error){border:1px solid var(--color-primary)}.jsd-select-wrapper .select-wrapper.expanded>.button::after,.jsd-select-wrapper .select-wrapper.expanded .input::after{transform:rotate(-135deg)}.jsd-select-wrapper .select-wrapper.expanded ul{height:auto;max-height:200px;border-top:1px solid var(--color-border)}.jsd-select-wrapper .select-wrapper.selected:not(.expanded)>.button{color:var(--color-black)}.jsd-select-wrapper .error-message,.jsd-select-wrapper .help-message{margin-top:3.3rem;position:relative}.jsd-select-wrapper.dark .select-wrapper>.button,.jsd-select-wrapper.dark .select-wrapper .input{color:var(--color-placeholder-darktheme)}.jsd-select-wrapper.dark .select-wrapper>.button::after,.jsd-select-wrapper.dark .select-wrapper .input::after{border-color:var(--color-placeholder-darktheme)}.jsd-select-wrapper.dark .select-wrapper ul{color:var(--color-white)}.jsd-select-wrapper.dark .select-wrapper ul li.current,.jsd-select-wrapper.dark .select-wrapper ul li.selected,.jsd-select-wrapper.dark .select-wrapper ul li:hover{background-color:var(--color-secondary-dark-darktheme)}.jsd-select-wrapper.dark .select-wrapper.focus{background-color:var(--color-background-darktheme)}.jsd-select-wrapper.dark .select-wrapper.expanded{background-color:var(--color-background-darktheme)}.jsd-select-wrapper.dark .select-wrapper.expanded ul{border-color:var(--color-secondary-dark-darktheme)}.jsd-select-wrapper.dark .select-wrapper.selected:not(.expanded)>.button,.jsd-select-wrapper.dark .select-wrapper.selected:not(.expanded) .input{color:var(--color-white)}`;