import {css} from 'lit-element';

export const style = css`.jsd-checkbox-wrapper .jsd-checkbox{display:block;padding:.5rem 1rem .5rem 0}.jsd-checkbox-wrapper.inline .jsd-checkbox{display:inline-block}.jsd-checkbox-wrapper label{position:relative;width:100%;display:inline-block;color:var(--color-label);cursor:pointer;z-index:1;display:flex;align-items:flex-start;z-index:1}.jsd-checkbox-wrapper label .checkbox{display:inline-block;vertical-align:middle;position:relative;height:calc(1rem + 2px);width:calc(1rem + 2px);margin-right:.5rem;margin-top:-0.1rem;border-radius:4px;border:1px solid var(--color-border);background-color:var(--color-secondary);flex-shrink:0;margin-top:0}.jsd-checkbox-wrapper label:not(.disabled):hover{color:var(--color-black)}.jsd-checkbox-wrapper label:not(.disabled):hover .checkbox{background-color:transparent}.jsd-checkbox-wrapper label.disabled{cursor:not-allowed;opacity:.8}.jsd-checkbox-wrapper .checkbox-input{position:absolute;margin:0;opacity:0}.jsd-checkbox-wrapper .checkbox-input:active+label:not(.disabled) .checkbox,.jsd-checkbox-wrapper .checkbox-input:focus+label:not(.disabled) .checkbox{background-color:transparent;border-color:var(--color-primary)}.jsd-checkbox-wrapper .checkbox-input:checked+label{color:var(--color-black)}.jsd-checkbox-wrapper .checkbox-input:checked+label .checkbox{background-color:var(--color-primary);border-color:var(--color-primary)}.jsd-checkbox-wrapper .checkbox-input:checked+label .checkbox::after{content:"";border:solid var(--color-white);border-width:0 2px 2px 0;display:inline-block;padding:4px 2px;left:5px;top:2px;position:absolute;transform:rotate(45deg)}.jsd-checkbox-wrapper .checkbox-input:checked+label:not(.disabled):hover .checkbox{background-color:var(--color-primary-dark)}.jsd-checkbox-wrapper .checkbox-input:checked:focus+label:not(.disabled) .checkbox{background-color:var(--color-primary-dark)}.jsd-checkbox-wrapper.error label .checkbox{border-color:var(--color-error);background-color:var(--color-error-background)}.jsd-checkbox-wrapper.error .checkbox-input+label .checkbox{border-color:var(--color-error)}.jsd-checkbox-wrapper.dark label{color:var(--color-label-darktheme)}.jsd-checkbox-wrapper.dark label .checkbox{background-color:var(--color-secondary-darktheme);border-color:var(--color-secondary-dark-darktheme)}.jsd-checkbox-wrapper.dark label:hover:not(.disabled){color:var(--color-white)}.jsd-checkbox-wrapper.dark label:hover:not(.disabled) .checkbox{background-color:transparent}.jsd-checkbox-wrapper.dark .checkbox-input:checked+label{color:var(--color-white)}.jsd-checkbox-wrapper.dark .checkbox-input:checked+label .checkbox{background-color:var(--color-primary)}.jsd-checkbox-wrapper.dark .checkbox-input:checked+label:not(.disabled):hover .checkbox{background-color:var(--color-primary-dark)}.jsd-checkbox-wrapper.dark.error label .checkbox{border-color:var(--color-error);background-color:var(--color-secondary-darktheme)}.jsd-checkbox-wrapper.dark.error label :not(.disabled):hover .checkbox{background-color:transparent}`;